.o-share {
  display: inline-block;
}

.o-share ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.o-share ul::after {
  clear: both;
  content: "";
  display: table;
}

.o-share__action {
  float: left;
  padding: 0;
  margin: 0 5px 0 0;
}

.o-share__action a {
  float: left;
  text-decoration: none;
  padding: 5px;
  margin: 0;
  border-radius: 3px;
}

.o-share__action i {
  float: left;
  width: 24px;
  height: 24px;
  white-space: nowrap;
  text-indent: -999em;
  overflow: hidden;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.o-share__action--url {
  position: relative;
}

.o-share__action--url .o-share-tooltip {
  position: absolute;
  white-space: nowrap;
  top: 50%;
  left: 100%;
  transform: translateY(-50%);
}

.o-share__action--url input {
  float: left;
  border: 0;
  border-radius: 2px;
  padding: 3px 8px;
  font-size: 14px;
  box-shadow: 0 0 3px #333333;
}

.o-share__action--url input::-ms-clear {
  display: none;
}

/* 	[data-o-share--js] .o-share__action--url {
		display: block;
	} */
.o-share__action--url[aria-selected=true] a {
  margin-right: 5px;
}

.o-share {
  position: absolute;
  right: 10px;
  bottom: 10px;
}

.o-share a {
  border: 2px solid white;
  border-radius: 50%;
}

.o-share a:hover {
  box-shadow: 0 0 3px 1px white;
}

.o-share .o-share__action--url {
  display: none;
}

