/* color */
/* $headerBackColor: #00609C; */
/* Should be divided by 3 */
/* How much should the separator bar be higher than ajacent box*/
/* 60 / 3 = 20px */
/* (60 - 20) / 2 - 4 = 16px */
body {
  font-family: "Hiragino Sans GB", "微软雅黑", "Microsoft JhengHei", SimSun, sans-serif;
  font-size: 18px;
  line-height: 1.618;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1.17em;
}

h5 {
  font-size: .83em;
}

h6 {
  font-size: .75em;
}

ul, ol {
  list-style: none;
}

a {
  color: #2e6e9e;
  text-decoration: none;
}

a:hover {
  color: #666;
}

iframe {
  margin-bottom: 10px;
}

.container {
  margin: 0 auto;
  text-align: left;
  overflow: hidden;
  *zoom: 1;
}

p {
  margin: 1em 0;
}

img {
  width: 100%;
}

.hidden {
  display: none;
}

/* Global include */
.video-outer {
  position: relative;
  height: 0;
  padding-top: 56.25%;
}

.video-outer .video-inner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* blockquotes and images */
.quote-block {
  padding: 1px 0px;
  margin: 50px 0px;
  text-align: center;
}

.quote-block blockquote {
  color: #00609C;
  font-weight: 400;
