}

.series-title {
  border-left: 2px solid rgba(255, 255, 255, 0.5);
  padding-left: 16px;
  margin-right: 0;
}

/* Navigation */
.small-menu {
  display: none;
}

.nav__toc {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 9999;
  line-height: 60px;
}

.nav__toc a {
  display: inline-block;
  color: rgba(255, 255, 255, 0.4);
  line-height: 30px;
  font-size: 15px;
  padding: 0 15px;
}

.nav__toc a.active {
  color: #ffffff;
}

/* General */
.text-over-media {
  position: relative;
  /*background-color: black;*/
  color: #ffffff;
  height: 100vh;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: center center;
  background-size: cover;
  /*(fixed)*/
}

.text-over-media * {
  position: relative;
}

.text-over-media .section-video {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  /*z-index: 1;*/
}

.text-over-media .section-head {
  font-size: 200%;
  text-align: center;
  transform: translate(-50%, 0);
  position: absolute;
  top: 5%;
  left: 50%;
  right: auto;
  padding: 0;
  line-height: 1.1;
  font-weight: normal;
}

.text-over-media .section-head:after {
  content: '';
  display: block;
  margin: 12px auto 0;
  height: 1px;
  background-color: white;
}

@media only screen and (max-width: 768px) {
  .text-over-media .section-head {
    font-size: 120%;
  }
}

.text-over-media .section-lead {
  text-align: center;
  font-size: 46.2px;
  margin: 0 auto;
  padding: 5%;
  position: absolute;
  bottom: 10%;
  left: 50%;
  transform: translate(-50%, 0);
  box-sizing: border-box;
  width: 100%;
  min-width: 80%;
}

@media only screen and (max-width: 768px) {
  .text-over-media .section-lead {
    font-size: 19.25px;
  }
}

.text-over-media::before {
  content: "";
  background-color: rgba(0, 0, 0, 0.2);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.inline-video {
  position: relative;
  background-color: #fff1e0;
  color: #ffffff;
  height: 60vh;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: center center;
  background-size: cover;
}

.inline-video video {
  object-fit: cover;
  background: transparent url("https://ig.ft.com/sites/land-rush-investment/myanmar/media/myanmar-still-mr_puptung.jpg") no-repeat 0 0;
  width: 100%;
  height: 100%;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.credits.text-over-media {
  padding: 1em;
}

.credits.text-over-media ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.content-column {
  max-width: 600px;
  margin: 0 auto;
  padding: 0 17px;
}

.content-column p {
  margin: .75em auto;
  line-height: 2em;
