}

/* Story header */
.cover-image {
  display: none;
}

.story-cover {
  box-sizing: border-box;
}

.story-cover .text-wrapper {
  box-sizing: border-box;
  width: 100%;
  padding: 5%;
  position: absolute;
  bottom: 60px;
  left: 0;
  right: 0;
  text-align: center;
}

.story-cover .text-wrapper p {
  font-size: 48px;
}

@media only screen and (max-width: 768px) {
  .story-cover .text-wrapper p {
    font-size: 32px;
  }
}

.story-cover h1 {
  margin: 0;
  font-size: 100px;
}

@media only screen and (max-width: 768px) {
  .story-cover h1 {
    font-size: 50px;
  }
}

.story-cover .article__time-byline {
  position: absolute;
  left: 0;
  bottom: 1em;
  width: 100%;
  text-align: center;
}

.story-cover .article__time-byline time, .story-cover .article__time-byline p {
  margin: 0;
  padding: 0;
  font-size: 16px;
}

.related-reading h4 {
  margin: 1em 0 0 0;
}

/* story footer */
.story-footer {
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center center;
  background-size: cover;
}

.story-footer p {
  max-width: 600px;
  text-align: center;
  margin: 0 auto;
  padding-top: 5em;
}

